.App {
  text-align: center;
  background: url(./assets/Space.png) no-repeat center center fixed;
  background-size: cover;
}

.App-logo {
  height: 5vmin;
}
.titleBar {
  background-color: black;
  display: block;
  color: #fff;
  font-size: 24;
  padding-left: 16px;
}
.inputText {
  font-size: 24;
  display: block;
  width: 99%;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 16px;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
